@import "./components/service-repair";


.repair-apart {   
    background-color: var(--stand-bg);
    padding-bottom: 24px;
    &__content{
        margin-top: 24px;
    }
    &__title{
        margin-bottom: 24px;
    }

    .nav-content-chat{
        margin-bottom: 16px;
        .nav-content__tags{
            margin-top: 8px;
            display: flex;
            align-items: center;
            gap: 8px;
        }
    }
    &__row {
        padding: 24px;
        background-color: var(--white);
        box-shadow: var(--shadow-primary);
        border-radius: 12px;

        &:not(:last-child) {
            margin-bottom: 16px;
        }

        @media (max-width:$desktop) {
            padding: 16px;
            border-radius: 12px;

            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }
    }
}
.repair-apart-about{
    &__items{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-column-gap: 16px;
        grid-row-gap: 16px;
    }
}

